import React, { useState } from 'react';

const BASE_URL = 'https://fitnessgroup3.herokuapp.com/';

function Confirmation()
{
    
    var loginEmail;

    const [message,setMessage] = useState('');
	
	const mystyle = {
		textAlign: 'center'
	}
	
	const VerifyAccount = async event =>
    {
        event.preventDefault();

//        alert('doIt()' + loginName.value + ' ' + loginPassword.value );

		var js = '{"Email":"'
            + loginEmail.value
            + '"}';

        try
        {    
            const response = await fetch(BASE_URL + 'confirmation/:verifyToken',
                {method:'POST',body:js,headers:{'Content-Type': 'application/json'}});

            

            if( response.status !== 200 )
            {
                setMessage('Error When Verifying');
            }
            else
            {
                setMessage('Verified');
                window.location.href = '/';
            }
        }
        catch(e)
        {
            alert(e.toString());
            return;
        }    

    };

    return (
		<div id="loginDiv">
			<form onSubmit={VerifyAccount} style={mystyle}>
                <span id="inner-title">Type In Your Email To Verify</span><br />
                <input type="text" id="loginEmail" placeholder="Email" ref={(c) => loginEmail = c} /><br />
                <input type="submit" id="loginButton" class="buttons" value="Verify Account" onClick={VerifyAccount} />
            </form>
            <span id="loginResult">{message}</span>
        </div>
    );
};

export default Confirmation;